* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
header {
  padding: 1rem 0;
  background: rgb(137, 171, 245);
  color: white;
  text-align: center;
  font-size: 2rem;
}
.list {
  list-style: none;
  padding: 1rem;
  max-width: 600px;
  margin: 0 auto;
}
.list > li {
  display: flex;
  padding: 0.75rem 0;

  justify-content: space-between;
  align-items: center;
}
.list > li > div:nth-child(1) .title {
  font-weight: bold;
  color: #232323;
}
.list > li > div:nth-child(1) .date {
  font-size: 0.75rem;
  color: gray;
}
.list > li > div:nth-child(2) {
  display: flex;
  align-items: center;
}
.list > li > div:nth-child(2) .btn_good {
  outline: none;
  border: none;
  padding: 0.5rem 1rem;
  background-color: transparent;
  cursor: pointer;
}
.list > li > div:nth-child(2) .good_counter {
  padding: 0.5rem;
}

/* 모달영역 */
.modal {
  position: fixed;
  width: 100%;
  height: 100vh;
  left: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
}
.modal_inner {
  background-color: #fff;
  padding: 1rem;
  border-radius: 1rem;
  min-width: 300px;
  width: 50%;
  max-width: 500px;
  position: relative;
}
.modal_inner h1 {
  text-align: center;
}

.modal_inner p {
  padding: 1rem 0;
}

.modal_inner .close {
  border: none;
  outline: none;
  background-color: lightblue;
  position: absolute;
  top: 0;
  right: -50px;
}
